a{
  text-decoration: none;
}

.refresh{
  animation: spinPulse 1s infinite ease-in-out;
  -moz-animation: spinPulse 1s infinite ease-in-out;
  transition: spinPulse 1s infinite linear;
  cursor: pointer;
}

@keyframes spinPulse {
  0% {
      transform: rotate(0deg);
  }
  50% {
      transform: rotate(180deg);
  }
  
100% {
  transform: rotate(360deg);
}
  ;
}


.css-yk16xz-control, .css-g1d714-ValueContainer{
  border-radius: 60px !important;
  height:56px;
}

.filteractive{
  transform: scale(1.1);
  transition: all 0.6s ease-in-out;
  box-shadow: 0px 14px 30px #00000029 !important;
}
.box {
    background-color: transparent;
    width: 280px;
    height: 180px;
    perspective: 1000px;
  }
  .box-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    animation: flipping 2s infinite;
    transform-style: preserve-3d;
    background: #061336;
    border-radius: 22px;
  }
  
  .box-front,
  .box-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background: #061336;
    border-radius: 22px;
  
  
  }
  .box-front {
   color: #111111;
  }
  .box-back {
    color: #eeeeee;
    transform: rotateY(180deg);
  }
  @keyframes flipping {
    0% {
      transform: rotateY(0deg);
    }
    50% {
      transform: rotateY(180deg);
    }
    100% {
      transform: rotateY(360deg);
    }
  }
  
